@import url(https://fonts.googleapis.com/css?family=Josefin+Slab:300,400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}::-webkit-scrollbar{width:10px}::-webkit-scrollbar-track{border-radius:10px;background-color:#444}::-webkit-scrollbar-thumb{background:#aaa;border-radius:10px}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.container{background-position:center;z-index:100;background-repeat:no-repeat;background-size:cover;width:100%;height:100vh;display:flex;justify-content:center;align-items:center}.wrapper{background:#313131;width:100%;max-width:450px;margin:0 15px;height:600px;padding:30px 20px;box-sizing:border-box;border-radius:25px;box-shadow:0px 0px 40px 50px rgba(0,0,0,0.7)}

.list-item{display:flex;align-items:center;justify-content:space-between;margin:10px 0;border-radius:20px;padding:15px  20px}.list-item:hover{background-color:rgba(0,0,0,0.2)}.list-item:first-of-type{margin-top:0}.list-item:last-of-type{margin-bottom:0}.list-item__text{color:#ffffff;margin-right:15px}.list-item__operations{display:flex}.list-item__button{cursor:pointer;background-color:transparent;border:none;outline:none;fill:#ffffff}.list-item__button:hover{fill:#bfbfbf}.list-item__button:active{fill:gray}.list-item__button:not(:last-child){margin-right:10px}@media screen and (max-width: 380px){.list-item__operations{flex-direction:column}.list-item__button:not(:last-of-type){margin-bottom:15px}}

.list__items{max-height:360px;overflow:hidden;overflow-y:auto;padding:0 15px}.list__empty-list{color:#777;position:absolute;left:50%;top:50%;transform:translate(-50%, -50%)}@media screen and (max-width: 380px){.list__items{max-height:300px}}

.new-item-form{display:flex;flex-direction:column;justify-content:center;align-items:center;margin-bottom:25px}.new-item-form__input{margin-bottom:20px;width:100%;padding:12px 25px;font-family:'Montserrat', sans-serif;font-size:15px;color:#000000;background-color:#ffffff;border:1px solid #444;outline:none;border-radius:20px;box-shadow:0 1px 2px rgba(0,0,0,0.3)}.new-item-form__input:hover{background-color:#a6a6a6}.new-item-form__operations{display:flex}.new-item-form__button{padding:10px;min-width:100px;background-color:#ffffff;color:#000000;border:none;font-family:'Montserrat', sans-serif;font-size:14px;border-radius:20px;box-shadow:0 2px 5px rgba(0,0,0,0.3);outline:none;cursor:pointer}.new-item-form__button:first-of-type:hover{background-color:#d51687}.new-item-form__button:first-of-type:active{background-color:#790c4c}.new-item-form__button:last-of-type:hover{background-color:#5ae1d0}.new-item-form__button:last-of-type:active{background-color:#19897b}.new-item-form__button:not(:last-child){margin-right:20px}@media screen and (max-width: 380px){.new-item-form__operations{flex-direction:column}.new-item-form__button:not(:last-child){margin-right:0px;margin-bottom:20px}}

.header__title{color:#ccc;font-weight:300;font-size:16px;text-align:center;margin-bottom:15px}

